export class LayerSwitcher extends React.Component<{
    mapFunc: () => Map,
    default?: string,
    layers?: string[]
},
    {
        expanded: boolean,
        selected: string,
        layers: string[],
        default: string
    }> {

    private _map: Map;

    constructor(props, context) {
        super(props, context);

        let layers = this.props.layers && this.props.layers.length > 0 ? this.props.layers : defaultLayerList;
        let defaultLayer = this.props.default || layers[0];

        this.state = {
            expanded: false,
            selected: null,
            layers: layers,
            default: defaultLayer
        };

        this._map = null;
    }

    get map(): Map {
        if (!this._map) {
            this._map = this.props.mapFunc();
        }
        return this._map;
    }

    switchLayers(newLayerId) {
        let previousSelected = this.state.selected;

        if (previousSelected !== null) {
            for (let ll of layerConfigs[previousSelected].layers) {
                this.map.removeLayer(ll);
            }
        }

        for (let ll of layerConfigs[newLayerId].layers) {
            this.map.addLayer(ll);
        }
        this.setState({selected: newLayerId});
    }

    componentDidMount() {
        let intv = setInterval(() => {
            if (this.map) {
                this.switchLayers(this.state.default);
                clearInterval(intv);
            }
        }, 100);
    }

    render() {

        // let thisLayerList = this.state.layers;

        let layerItems = [];

        for (let i = 0; i < this.state.layers.length; i++) {

            let counter = 0;
            let divList = [];


            for (i; i < this.state.layers.length && counter < 3; i++, counter++) {
                let layerId = this.state.layers[i];
                let conf = layerConfigs[layerId];


                divList.push(<div key={conf.name} style={{
                    display: 'inline-block',
                    padding: '4px',
                    margin: '3px',
                    border: 'solid black 1px',
                    borderRadius: '7px',
                    backgroundColor: this.state.selected === layerId ? 'lightblue' : '',
                    cursor: 'pointer'
                }} onClick={() => {
                    this.switchLayers(layerId);
                }}>
                    <img src={conf.thumbnail} width={120}/>
                    <h5 style={{textAlign: 'center', margin: '2px 0'}}>{conf.name}</h5>
                </div>)
            }

            layerItems.push(<li key={`li-${i}`}>
                {divList}
            </li>);

            i--;
        }

        let content = null;
        if (this.state.expanded) {
            content = <ul style={{
                listStyle: 'none',
                padding: '0',
                margin: '0',
                maxHeight: '250px',
                // maxHeight: '200px',
                // overflowY: 'scroll',
                // marginRight: '40px',
            }}>
                {layerItems}
            </ul>
        } else {
            content =
                <div style={{display: 'block', backgroundColor: 'lightblue'}}
                      title="Base Map Switcher" onClick={
                    () => {
                        this.setState({expanded: !this.state.expanded})
                    }
                }>
                    <img width="30" style={{padding: '3px'}} src="data:image/png;base64,"/>
                </div>

        }

        let timeOut: number = null;

        return <div
            style={{
                position: 'absolute',
                left: '50px',
                top: '20px',
                backgroundColor: 'white',
                zIndex: 5,
                overflowY: this.state.expanded ? 'scroll' : 'initial',
                overflowX: 'hidden',
                border: 'solid black 1px',
                borderRadius: '7px',
            }}
            onMouseLeave={
                () => {
                    timeOut = setTimeout(() => {
                        if (this.state.expanded) {
                            this.setState({expanded: !this.state.expanded})
                        }
                    }, 750)
                }
            }
            onMouseEnter={
                () => {
                    if (this.state.expanded && timeOut) {
                        clearTimeout(timeOut);
                    }

                }
            }
        >
            {content}


        </div>
    }
}